import tippy from 'tippy.js';
import 'tippy.js/dist/tippy.css';
import 'tippy.js/themes/light-border.css';
const _global = (window /* browser */ || global /* node */);
_global.$tippy = tippy;
import { TabulatorFull as Tabulator } from 'tabulator-tables';
export class App {
    recipesUrl = '';
    itemsJsonUrl = 'assets/21-items.json';
    imagesPath = '21';
    recipes = [];
    items = [];
    recipes_ingredients = new Map();
    recipes_buffs = new Map();
    recipes_buffs_filtered = new Map();
    ingredients = [];
    tabledata = [];
    ingredientCols = [];
    table;
    source;
    init() {
        const queryString = window.location.search;
        const urlParams = new URLSearchParams(queryString);
        const sourceParam = urlParams.get('source');
        this.source = sourceParam || "";
        this.recipesUrl = `assets/${this.source}21-recipes.json`;
        this.itemsJsonUrl = `assets/${this.source}21-items.json`;
        this.imagesPath = `assets/${this.source}21`;
        Promise.all([fetch(this.recipesUrl), fetch(this.itemsJsonUrl)])
            .then((recipesAnditemsPromisesArray) => {
            Promise.all([recipesAnditemsPromisesArray[0].json(), recipesAnditemsPromisesArray[1].json()])
                .then((values) => {
                this.recipes = values[0];
                this.items = values[1];
                this.app();
            });
        });
    }
    isDarkMode() {
        // body[data-bs-theme="dark"]
        return document.body.dataset.bsTheme === "dark";
    }
    createPopOver(id, content) {
        tippy('#' + id, {
            content: `${content}`,
            allowHTML: true,
            theme: this.isDarkMode() ? 'dark-border' : 'light-border',
            arrow: true,
            trigger: 'click',
            interactive: true,
            animation: 'scale',
        });
    }
    itemByName(name) {
        const arr = this.items.filter((item) => item.name === name);
        return arr && arr.length > 0 ? arr[0] : null;
    }
    /*itemPropertyByName(item: Item, name: string): Property | undefined {
        return item.property ? this.propertyByName(item.property, name) : undefined;
    }*/
    merge(parent, child) {
        const mergedItem = { name: child.name, property: undefined, effect_group: undefined };
        mergedItem.property = this.mergeProperties(parent?.property, child.property);
        mergedItem.effect_group = this.mergeEffectGroup(this.pickEffectGroup(parent?.effect_group), this.pickEffectGroup(child.effect_group));
        return mergedItem;
    }
    mergeEffectGroup(parentEffectGroup, childEffectGroup) {
        const mergedEffectGroup = { display_value: undefined, triggered_effect: undefined };
        mergedEffectGroup.display_value = this.mergeProperties(parentEffectGroup?.display_value, childEffectGroup?.display_value);
        mergedEffectGroup.triggered_effect = this.mergeTriggeredEffect(parentEffectGroup?.triggered_effect, childEffectGroup?.triggered_effect);
        return mergedEffectGroup;
    }
    mergeTriggeredEffect(parentTriggeredEffect, childTriggeredEffect) {
        const mergedTriggeredEffect = this.clone(this.triggeredEffectsToArray(parentTriggeredEffect));
        const childTriggeredEffectArray = this.triggeredEffectsToArray(childTriggeredEffect);
        childTriggeredEffectArray.forEach((triggeredEffect) => {
            if (triggeredEffect.cvar) {
                const existingTriggeredEffect = this.triggeredEffectByCvar(mergedTriggeredEffect, triggeredEffect.cvar);
                if (existingTriggeredEffect) {
                    existingTriggeredEffect.value = triggeredEffect.value;
                }
                else {
                    mergedTriggeredEffect.push(triggeredEffect);
                }
            }
            else if (triggeredEffect.buff) {
                const existingTriggeredEffect = this.triggeredEffectByBuff(mergedTriggeredEffect, triggeredEffect.buff);
                if (existingTriggeredEffect) {
                    existingTriggeredEffect.value = triggeredEffect.value;
                }
                else {
                    mergedTriggeredEffect.push(triggeredEffect);
                }
            }
            else if (triggeredEffect.operation) {
                if (triggeredEffect.operation === 'add') {
                    const existingTriggeredEffect = this.triggeredEffectByBuff(mergedTriggeredEffect, triggeredEffect.stat);
                    if (existingTriggeredEffect) {
                        existingTriggeredEffect.value = triggeredEffect.value;
                    }
                    else {
                        mergedTriggeredEffect.push(triggeredEffect);
                    }
                }
                else {
                    console.error("triggeredEffect operation/stat incorrect", triggeredEffect);
                }
            }
            else {
                if (triggeredEffect.action !== "CallGameEvent" && triggeredEffect.action !== "PlaySound") {
                    console.error("triggeredEffect must have cvar, buff or operation", triggeredEffect);
                }
            }
        });
        return mergedTriggeredEffect;
    }
    triggeredEffectByCvar(triggeredEffectArray, cvar) {
        return triggeredEffectArray.filter((triggeredEffect) => triggeredEffect.cvar === cvar)[0];
    }
    triggeredEffectByBuff(triggeredEffectArray, buff) {
        return triggeredEffectArray.filter((triggeredEffect) => triggeredEffect.buff === buff)[0];
    }
    effectGroupsToArray(effectGroup) {
        if (!effectGroup) {
            return [];
        }
        else if (Array.isArray(effectGroup)) {
            return effectGroup;
        }
        else {
            return [effectGroup];
        }
    }
    triggeredEffectsToArray(triggeredEffect) {
        if (!triggeredEffect) {
            return [];
        }
        else if (Array.isArray(triggeredEffect)) {
            return triggeredEffect;
        }
        else {
            return [triggeredEffect];
        }
    }
    ingredientsToArray(ingredient) {
        if (!ingredient) {
            return [];
        }
        else if (Array.isArray(ingredient)) {
            return ingredient;
        }
        else {
            return [ingredient];
        }
    }
    propertiesToArray(properties) {
        if (!properties) {
            return [];
        }
        else if (Array.isArray(properties)) {
            return properties;
        }
        else {
            return [properties];
        }
    }
    clone(item) {
        return JSON.parse(JSON.stringify(item));
    }
    mergeProperties(parentProperties, childProperties) {
        return this.mergePropertiesArrays(this.propertiesToArray(parentProperties), this.propertiesToArray(childProperties));
    }
    mergePropertiesArrays(parentProperties, childProperties) {
        const mergedProperties = this.clone(parentProperties);
        childProperties.forEach((property) => {
            const existingProperty = this.propertyByName(mergedProperties, property.name);
            if (existingProperty) {
                existingProperty.value = property.value;
            }
            else {
                mergedProperties.push(property);
            }
        });
        return mergedProperties;
    }
    pickEffectGroup(effectGroup) {
        if (!effectGroup)
            return undefined;
        if (Array.isArray(effectGroup)) {
            const combinedEffectGroup = { display_value: undefined, triggered_effect: undefined };
            effectGroup.forEach((effectGroup) => {
                combinedEffectGroup.display_value = this.mergeProperties(combinedEffectGroup.display_value, effectGroup.display_value);
                combinedEffectGroup.triggered_effect = this.mergeTriggeredEffect(combinedEffectGroup.triggered_effect, effectGroup.triggered_effect);
            });
            return effectGroup[0];
        }
        return effectGroup;
    }
    app() {
        this.recipes.forEach((recipe) => {
            let item = this.itemByName(recipe.name);
            if (item) {
                const ingredientsArray = [];
                this.recipes_ingredients.set(recipe, ingredientsArray);
                const buffsArray = [];
                this.recipes_buffs.set(recipe, buffsArray);
                const buffsFilteredArray = [];
                this.recipes_buffs_filtered.set(recipe, buffsFilteredArray);
                this.ingredientsToArray(recipe.ingredient).forEach((ingredient) => ingredientsArray.push(ingredient));
                let parentProperty = this.propertyValueByName(item.property, "Extends");
                if (parentProperty) {
                    const itemName = item.name;
                    const parent = this.itemByName(parentProperty);
                    item = this.merge(parent, item);
                    item.name = itemName;
                    parentProperty = this.propertyValueByName(item.property, "Extends");
                }
                else {
                    item = this.merge(item, item);
                }
                const itemEffectGroup = this.pickEffectGroup(item.effect_group);
                if (itemEffectGroup?.display_value) {
                    if (Array.isArray(itemEffectGroup.display_value)) {
                        itemEffectGroup.display_value.forEach((displayValue) => {
                            buffsArray.push({ name: displayValue.name, value: displayValue.value });
                        });
                    }
                    else {
                        buffsArray.push({
                            name: itemEffectGroup.display_value.name,
                            value: itemEffectGroup.display_value.value
                        });
                    }
                }
                if (itemEffectGroup?.triggered_effect) {
                    this.triggeredEffectsToArray(itemEffectGroup.triggered_effect).forEach((triggeredEffect) => {
                        if (triggeredEffect.cvar && triggeredEffect.value) {
                            buffsArray.push({ name: triggeredEffect.cvar, value: triggeredEffect.value });
                        }
                    });
                }
                buffsArray.forEach((buff) => {
                    const r = buff.name
                        .replace(".foodStaminaBonusAdd", "STA")
                        .replace("foodHealthAmount", "HP")
                        .replace("$foodAmountAdd", "FOOD")
                        .replace("$waterAmountAdd", "H2O")
                        .replace("dWellness", "WELL")
                        .replace("dWaterAmountAdd", "H2O")
                        .replace("dFoodAmount", "FOOD")
                        .replace("dFoodStaminaBonus", "STA")
                        .replace("dFoodHealthAmount", "HP")
                        .replace("dStressReduction", "CALM")
                        .replace("FoodPoisoningRisk", "POISON");
                    if (r !== buff.name) {
                        buff.name = r;
                        if (buffsFilteredArray.filter((buffFiltered) => buffFiltered.name === buff.name).length == 0) {
                            buffsFilteredArray.push(buff);
                        }
                    }
                });
            }
        });
        const keysArray = Array.from(this.recipes_buffs_filtered.keys());
        this.recipes = this.recipes.filter((recipe) => keysArray.indexOf(recipe) !== -1);
        this.recipes_ingredients.forEach((ingredients, itemName) => {
            if (keysArray.indexOf(itemName) === -1) {
                this.recipes_ingredients.delete(itemName);
            }
        });
        this.recipes_ingredients.forEach((ingredients, itemName) => {
            ingredients.forEach((ingredient) => {
                if (this.ingredients.indexOf(ingredient.name) === -1) {
                    this.ingredients.push(ingredient.name);
                }
            });
        });
        // sort ingredients
        this.ingredients.sort((a, b) => {
            if (a < b) {
                return -1;
            }
            else if (a === b) {
                return 0;
            }
            else {
                return 1;
            }
        });
        // sort recipes by name
        this.recipes.sort((a, b) => {
            if (a.name < b.name) {
                return -1;
            }
            else if (a.name === b.name) {
                return 0;
            }
            else {
                return 1;
            }
        });
        this.buildIngredientStyles();
        this.buildIngredientCols();
        this.buildTableData();
        this.buildFilters();
        this.buildTable();
    }
    iconForItem(name) {
        const item = this.itemByName(name);
        let icon = name;
        if (item) {
            const parent = this.propertyValueByName(item.property, "Extends");
            let iconProperty = this.propertyValueByName(item.property, "CustomIcon");
            if (iconProperty)
                icon = iconProperty;
            if (parent) {
                const parentItem = this.itemByName(parent);
                if (parentItem) {
                    icon = parentItem.name;
                    iconProperty = this.propertyValueByName(parentItem.property, "CustomIcon");
                    if (iconProperty)
                        icon = iconProperty;
                }
            }
        }
        return icon;
    }
    buildIngredientStyles() {
        let style = '';
        let ingredientList = '';
        for (const ingredient of this.ingredients) {
            const icon = this.iconForItem(ingredient);
            style += `.tabulator-col.tabulator-col-vertical.tabulator-sortable.tabulator-col-sorter-element.${ingredient} .tabulator-col-title::before { background-image: url('${this.imagesPath}/${icon}.png'); }\n\n`;
            ingredientList += icon + '\n';
        }
        console.log(style);
        console.log(ingredientList);
        const styleElement = document.createElement('style');
        styleElement.innerHTML = style;
        document.head.appendChild(styleElement);
    }
    buildIngredientCols() {
        for (const ingredient of this.ingredients) {
            this.ingredientCols.push({
                "title": ingredient
                    .replace('food', '').replace('Food', '')
                    .replace('drink', '').replace('Drink', '')
                    .replace('resource', '').replace('Resource', '')
                    .replace('crop', '').replace('Crop', ''),
                "field": `ingredient_${ingredient}`,
                "sorter": "number",
                "headerVertical": true,
                "hozAlign": "center",
                "cssClass": ingredient
            });
        }
    }
    propertyValueByName(properties, name) {
        return this.propertyByName(properties, name)?.value;
    }
    propertyByName(properties, name) {
        if (!properties) {
            return undefined;
        }
        else if (Array.isArray(properties)) {
            return properties.filter((property) => property.name === name)[0];
        }
        else {
            return properties.name === name ? properties : undefined;
        }
    }
    buildTableData() {
        let foodId = 0;
        for (const recipe of this.recipes) {
            const buffs = this.recipes_buffs_filtered.get(recipe);
            const ingredients = this.recipes_ingredients.get(recipe);
            const item = this.itemByName(recipe.name);
            const icon = this.iconForItem(recipe.name);
            /*const item = this.itemByName(recipe.name);
            const parent = this.propertyValueByName(item.property, "Extends");
            let icon = recipe.name;
            let iconProperty = this.propertyValueByName(item.property, "CustomIcon");
            if (iconProperty) icon = iconProperty;
            if (parent) {
                const parentItem = this.itemByName(parent);
                if (parentItem) {
                    icon = parentItem.name;
                    iconProperty = this.propertyValueByName(parentItem.property, "CustomIcon");
                    if (iconProperty) icon = iconProperty;
                }
            }*/
            const row = {
                id: foodId,
                name: recipe.name
                    .replace('food', '').replace('Food', '')
                    .replace('drink', '').replace('Drink', '')
                    .replace('resource', '').replace('Resource', '')
                    .replace('crop', '').replace('Crop', ''),
                craftArea: recipe.craft_area ? recipe.craft_area.toLowerCase() : '',
                craftTool: recipe.craft_tool ? recipe.craft_tool.replace('tool', '').toLowerCase() : '',
                count: Number(recipe.count),
                food: this.toNumber(this.propertyValueByName(buffs, "FOOD")),
                water: this.toNumber(this.propertyValueByName(buffs, "H2O")),
                hp: this.toNumber(this.propertyValueByName(buffs, "HP")),
                stamina: this.toNumber(this.propertyValueByName(buffs, "STA")),
                wellness: this.toNumber(this.propertyValueByName(buffs, "WELL")),
                calmness: this.toNumber(this.propertyValueByName(buffs, "CALM")),
                poison: this.toNumber(this.propertyValueByName(buffs, "POISON")),
                recipe: recipe,
                ingredients: ingredients || [],
                buffs: buffs || [],
                icon: icon
            };
            for (const ingredient of this.ingredients) {
                const count = ingredients?.filter((recipeIngredient) => recipeIngredient.name === ingredient)[0]?.count;
                if (count)
                    row[`ingredient_${ingredient}`] = /*count ?*/ String(count) /*: ''*/;
            }
            this.tabledata.push(row);
            foodId++;
            if (recipe.name.toLowerCase().includes("hobostew")) {
                console.log("recipe", recipe.name, recipe, item, "craft area", recipe.craft_area);
            }
            if (recipe.craft_area) {
                const craftArea = recipe.craft_area.toLowerCase().trim();
                if (this.craftAreas.indexOf(craftArea) === -1) {
                    this.craftAreas.push(craftArea);
                }
            }
        }
    }
    buildTable() {
        const columDefs = [];
        const sorter = function (a, b, aRow, bRow, column, dir, sorterParams) {
            //a, b - the two values being compared
            //aRow, bRow - the row components for the values being compared (useful if you need to access additional fields in the row data for the sort)
            //column - the column component for the column being sorted
            //dir - the direction of the sort ("asc" or "desc")
            //sorterParams - sorterParams object from column definition array
            if (!a)
                a = 0;
            if (!b)
                b = 0;
            return a - b; //you must return the difference between the two values
        };
        columDefs.push({
            title: "Name",
            field: "name",
            frozen: true,
            formatter: (cell, formatterParams) => {
                const row = cell.getData();
                const cellValue = cell.getValue();
                const a = row.ingredients.map((ingredient) => `${ingredient.count} ${ingredient.name}`).join("\n");
                const b = row.buffs.map((buff) => `${buff.value} ${buff.name}`).join("\n");
                return `<img width=32 height=32 src="${this.imagesPath}/${row.icon}.png">&nbsp;<span title='${a}\n\n${b}'>${cellValue}</span>`;
            }
        });
        const infoGroupColumDef = {
            title: "Info",
            frozen: true,
            columns: []
        };
        infoGroupColumDef.columns?.push({
            title: "Craft Area",
            field: "craftArea",
            sorter: "string",
            headerVertical: false,
            hozAlign: "center"
        });
        infoGroupColumDef.columns?.push({
            title: "Craft Tool",
            field: "craftTool",
            sorter: "string",
            headerVertical: false,
            hozAlign: "center"
        });
        infoGroupColumDef.columns?.push({
            title: "Count",
            field: "count",
            sorter: "number",
            headerVertical: true,
            hozAlign: "center"
        });
        columDefs.push(infoGroupColumDef);
        const buffGroupColumDef = {
            title: "Buffs",
            frozen: true,
            columns: []
        };
        buffGroupColumDef.columns?.push({
            title: "FOOD",
            field: "food",
            sorter: sorter,
            headerVertical: true,
            hozAlign: "center"
        });
        buffGroupColumDef.columns?.push({
            title: "H2O",
            field: "water",
            sorter: sorter,
            headerVertical: true,
            hozAlign: "center"
        });
        buffGroupColumDef.columns?.push({
            title: "HP",
            field: "hp",
            sorter: sorter,
            headerVertical: true,
            hozAlign: "center"
        });
        buffGroupColumDef.columns?.push({
            title: "STA",
            field: "stamina",
            sorter: sorter,
            headerVertical: true,
            hozAlign: "center"
        });
        buffGroupColumDef.columns?.push({
            title: "WELL",
            field: "wellness",
            sorter: sorter,
            headerVertical: true,
            hozAlign: "center"
        });
        buffGroupColumDef.columns?.push({
            title: "CALM",
            field: "calmness",
            sorter: sorter,
            headerVertical: true,
            hozAlign: "center"
        });
        buffGroupColumDef.columns?.push({
            title: "POISON (%)",
            field: "poison",
            sorter: sorter,
            headerVertical: true,
            hozAlign: "center",
            formatter: (cell, formatterParams) => {
                const row = cell.getData();
                return '<span>' + (row.poison ? row.poison * 100 : '') + '</span>';
            }
        });
        columDefs.push(buffGroupColumDef);
        columDefs.push({
            title: "Ingredients",
            columns: this.ingredientCols
        });
        const options = {
            placeholder: "Awaiting Data",
            height: '100%',
            data: this.tabledata,
            //layout: "fitColumns", //fit columns to width of table (optional)
            //layout: "fitDataTable",
            layoutColumnsOnNewData: true,
            // responsiveLayout:"hide", // hide rows that no longer fit
            // responsiveLayout:"collapse", // collapse columns that no longer fit on the table into a list under the row
            resizableRows: false,
            selectable: false,
            columns: columDefs,
            rowHeight: 40,
        };
        this.table = new Tabulator("#table-7d2d-food", options);
    }
    toNumber(s) {
        try {
            return s ? Number(s) : null;
        }
        catch (error) {
            console.error(s, error);
            return null;
        }
    }
    craftAreas = [];
    filterCraftArea;
    nameFilter;
    hideDebuffs;
    buildFilters() {
        this.filterCraftArea = $('#filterCraftArea');
        this.nameFilter = $('#nameFilter');
        this.hideDebuffs = $('#hideDebuffs');
        const clearFiltering = () => {
            this.table.clearFilter(true);
        };
        const updateFilter = () => {
            clearFiltering();
            //
            const filters = [];
            //
            const filterCraftAreaVal = this.filterCraftArea.val();
            if (filterCraftAreaVal && filterCraftAreaVal.length > 0) {
                filters.push([{
                        field: "craftArea", type: "in", value: filterCraftAreaVal
                    }]);
            }
            //
            const nameFilterVal = this.nameFilter.val();
            if (nameFilterVal && nameFilterVal.length > 0) {
                const colFilters = nameFilterVal.split(',').map((x) => x.trim())
                    .map((x) => {
                    return { "field": "name", "type": "like", "value": x };
                });
                colFilters.forEach(colFilter => filters.push([colFilter]));
            }
            //
            const hideDebuffsVal = this.hideDebuffs.prop('checked');
            if (hideDebuffsVal) {
                ["poison"].forEach((it) => {
                    filters.push([
                        { field: it, type: "<", value: 0 },
                        { field: it, type: "=", value: 0 },
                        { field: it, type: "=", value: null }
                    ]);
                });
                ["calmness", "wellness", "stamina", "hp", "water", "food"].forEach((it) => {
                    filters.push([
                        { field: it, type: ">", value: 0 },
                        { field: it, type: "=", value: 0 },
                        { field: it, type: "=", value: null }
                    ]);
                });
            }
            //
            if (filters.length > 0) {
                console.log("filters", filters);
                this.table.setFilter(filters);
            }
        };
        this.buildFilterCraftArea();
        document.getElementById('filter').addEventListener('click', (event) => {
            updateFilter();
        });
        document.getElementById('filterClear').addEventListener('click', (event) => {
            this.filterCraftArea.selectpicker('val', this.craftAreas);
            clearFiltering();
        });
    }
    buildFilterCraftArea() {
        for (const craftArea of this.craftAreas) {
            const option = new Option(craftArea, craftArea);
            option.selected = true;
            this.filterCraftArea.append(option);
        }
        this.filterCraftArea.selectpicker();
    }
}
